#ceo-page-wrapper {
  height: 550px;
}

.ceo-page-container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  gap: 20px;
  margin-top: 70px;
}

.ceo-page-top {
  display: flex;
  flex-direction: column;
  justify-content: center;

  gap: 10px;
}

.ceo-page-top-title {
  text-align: center;

  font-family: 'KCC-Hanbit';
  font-size: 30px;
  font-weight: bold;
  line-height: 150%;
  color: rgba(50, 50, 50, 1);
}

.ceo-page-navigation-box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.ceo-page-navigation {
  width: 100px;
  height: 20px;

  padding: 10px;

  border: 1px solid rgba(245, 245, 245, 1);

  font-family: 'KCC-Hanbit';
  font-size: 17px;
  font-weight: bold;
  color: rgba(50, 50, 50, 1);
}

.ceo-page-navigation:hover {
  cursor: pointer;
  background-color: rgba(255, 148, 45, 0.2);
}

.ceo-page-contents-nav-box {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: center;

  gap: 30px;
  
  font-family: 'NanumSquareRound';
  font-size: 17px;
  font-weight: bold;
  color: rgba(50, 50, 50, 1);
}

.ceo-page-contents-nav {
  padding: 10px;
  border-radius: 7px;

  text-align: center;
}

.ceo-page-nav:hover {
  cursor: pointer;
  color: rgba(255, 148, 45, 0.8);
}

.ceo-page-info-contents-container {
  width: 1100px;

  display: flex;
  flex-direction: column;

  gap: 10px;
  margin-top: 20px;
}

.ceo-page-contents-title {
  width: 100%;

  display: flex;
  justify-content: flex-start ;

  font-family: 'KCC-Hanbit';
  font-size: 20px;
  font-weight: bold;
  line-height: 150%;
  color: rgba(50, 50, 50, 1);
}

.ceo-page-contents-box {
  width: 100%;

  display: grid;
  flex-direction: column;
  grid-template-columns: 1fr 1fr;
}

.ceo-page-info-first {
  width: 100%;

  display: flex;
  flex-direction: row;

  border-top: 1px solid rgba(238, 238, 238, 1);

  word-break: break-word;
}

.ceo-page-info-second {
  width: 100%;

  display: flex;
  flex-direction: row;
  word-break: break-word;

  border-top: 1px solid rgba(238, 238, 238, 1); 
  border-bottom: 1px solid rgba(238, 238, 238, 1); 
}

.ceo-page-info {
  width: 100%;
  min-width: 100px;
  
  text-align: left;

  padding: 10px;

  background-color: rgba(255, 255, 255, 1);

  font-family: 'NanumSquareRound';
  font-size: 15px;
  font-weight: 400;
  line-height: 150%;
  color: rgba(50, 50, 50, 1);

  word-break: break-word; 
}

.ceo-page-resign {
  width: 150px;

  text-align: center;

  padding: 10px;

  background-color: rgba(255, 148, 45, 0.7);
  border-radius: 8px;

  font-family: 'NanumSquareRound';
  font-size: 15px;
  font-weight: 400;
  line-height: 150%;
  color: rgba(255, 255, 255, 1);
}

.ceo-page-resign:hover {
  cursor: pointer;
  opacity: 0.85;
}